* {
  padding: 0;
  margin: 0;
}
body {
  font-family: 'Montserrat-Regular', sans-serif;
  font-size: 16px;
}
img {
  display: block;
  box-sizing: content-box;
}
a {
  text-decoration: none;
}
button {
  background-color: unset;
  border: none;
}
main {
  width: 1220px;
  margin: 0 auto;
}
